export function* range(from, to) {
    while (from < to) {
        yield from++;
    }
}
export function randFloat(bottom, top) {
    return Math.random() * (top - bottom) + bottom;
}
export function randInt(bottom, top) {
    return Math.floor(randFloat(bottom, top));
}
export function randChoice(arr) {
    return arr[randInt(0, arr.length)];
}
export function fixNumber(min, max, number) {
    return number >= min ? number % max : max - (-number % max);
}
export function normalizeNumber(min, max, number) {
    return (number - min) / (max - min);
}
export function limNumber(min, max, number) {
    return Math.max(Math.min(number, max), min);
}
export function interpolate(a, b, t) {
    return a + (b - a) * t;
}
